//input
.input_wrap {
  input {
    display: inline-block;
    border: none;
    border-bottom: 1px solid #000;
    text-align: center;
    font-size: 16px;
    padding: 5px 0;
  }
}

//button
.btn_wrap {
  button {
    display: inline-block;
    padding: 10px 20px;
    background-color: transparent;
    border: none;
    border-radius: 5px;
  }
  &.sm {
    button {
      padding: 5px 10px;
    }
  }
}

//flex
.flex{
  display: flex;
}

.flex_center {
  display: flex;
  justify-content: center;
  align-items: center;
}

