* {
  font-family: 'GmarketSans', 'Noto Sans KR', sans-serif;
  margin: 0;
  padding: 0;
  word-break: keep-all;
  color: #222222;
}

input,textarea{-webkit-appearance:none;-webkit-border-radius:0;}
input[type=checkbox]{-webkit-appearance:checkbox;}
input[type=radio]{-webkit-appearance:radio;}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,acronym,address,big,cite,code,del,dfn,em,font,img,ins,kbd,q,s,samp,
small,strike,strong,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td{margin:0;padding:0;border:0;outline:0;vertical-align:baseline;}
input{margin:0;padding:0;background-color:#fff;}
input[disabled],input[disabled]:hover,input[disabled]:focus,input[readonly],input[readonly]:hover,input[readonly]:hover,textarea[disabled],textarea[disabled]:hover,textarea[disabled]:focus,textarea[readonly],textarea[readonly]:hover,textarea[readonly]:focus{background-color:#fff !important;}
input[type=submit],input[type=button],button{cursor:pointer;}
textarea{resize:none;}
fieldset,img,abbr,acronym{border:0 none;}
ul,ul li, li{list-style:none !important; padding: 0;}
table{border-collapse:collapse;border-spacing:0;border:0 none;}
caption,th,td{text-align:left;vertical-align:middle;font-weight:normal;}
caption{font-size:0;height:0;line-height:0;text-indent:-9999px;width:0;}
img{vertical-align:top;}
label,select,input,textarea{vertical-align:middle;}
select optgroup{font-style:normal;}
i,em,address{font-style:normal;font-weight:normal;}
legend{position:absolute;top:0;left:0;overflow:hidden;}
hr{left:0;overflow:hidden;position:absolute;top:0;}
sub,sup,i.sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline;}
sub{bottom:-0.25em;}
sup,i.sup{top:-0.5em;}
label,.cursor_pointer{cursor:pointer;}
*:focus{outline: none !important; }
*{ outline: none !important; text-decoration: none !important; box-sizing: border-box; }
* { letter-spacing: -0.5px; background-repeat: no-repeat; }
a { color: black; }
a:active { color: inherit; }
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {-webkit-appearance: none; margin: 0;}
